<template>
  <div class="submit-form">
    <div v-if="!submitted">
      <div class="alert alert-danger" v-if="errors.length">
        <li v-for="(error, index) in errors" :key="index">
          {{ error }}
        </li>
      </div>
      <div class="form-group mb-1">
        <label for="name">Nome *</label>
        <input
          type="text"
          class="form-control"
          id="name"
          required
          v-model="client.name"
          name="name"
        />
      </div>

      <div class="form-group mb-1">
        <label for="phone">Telefone</label>
        <input
          class="form-control"
          id="phone"
          required
          v-model="client.phone"
          name="phone"
          v-mask="'(##) #####-####'"
        />
      </div>

      <div class="form-group mb-1">
        <label for="cpf">CPF *</label>
        <input
          class="form-control"
          id="cpf"
          required
          v-model="client.cpf"
          name="cpf"
          v-mask="'###.###.###-##'"
        />
      </div>

      <div class="form-group mb-1">
        <label for="birth_date">Data de Nascimento *</label>
        <input
          class="form-control"
          id="birth_date"
          required
          v-model="client.birth_date"
          type="date"
          name="birth_date"
        />
      </div>
      <button @click="saveClient" class="btn btn-success">Salvar</button>
    </div>
    <div v-else>
      <h4>Cliente salvo com sucesso!</h4>
      <button class="btn btn-success" @click="newClient">Confirmar</button>
    </div>
  </div>
</template>

<script>
import ClientDataService from "../services/ClientDataService";

export default {
  name: "form-client",
  data() {
    return {
      client: {
        id: null,
        name: "",
        cpf: "",
        phone: "",
        birth_date: "",
      },
      submitted: false,
      errors: [],
    };
  },
  methods: {
    retrieveClient(id) {
      ClientDataService.get(id)
        .then((response) => {
          this.client = response.data.data;
          console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },
    saveClient() {
      this.errors = [];
      if (!this.client.cpf) {
        this.errors.push("Por fovor preencha o CPF.");
      }
      if (!this.client.name) {
        this.errors.push("Por fovor preencha o nome do cliente.");
      }
      if (!this.client.birth_date) {
        this.errors.push("Por fovor preencha a data de nascimento.");
      }
      if (this.errors.length === 0) {
        var data = {
          name: this.client.name,
          cpf: this.client.cpf,
          phone: this.client.phone,
          birth_date: this.client.birth_date,
        };
        if (!this.id) {
          ClientDataService.create(data)
            .then((response) => {
              this.client.id = response.data.id;
              console.log(response.data);
              this.submitted = true;
            })
            .catch((e) => {
              console.log(e);
            });
        } else {
          ClientDataService.update(this.id, data)
            .then((response) => {
              this.client.id = response.data.id;
              this.submitted = true;
            })
            .catch((e) => {
              console.log(e);
            });
        }
      }
    },

    newClient() {
      this.submitted = false;
      this.client = {};
    },
  },
  mounted() {
    this.id = this.$route.params.id;

    if (this.id) {
      this.retrieveClient(this.id);
    }
  },
};
</script>

<style>
.submit-form {
  max-width: 300px;
  margin: auto;
}
</style>